.transportations-search-form {
  $block-name: &;

  &__title {
    margin-bottom: 45px;
  }

  &__fields-wrap {
    position: relative;

    #{$block-name}__field-text input {
      padding: 11px 35px;
    }

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 15px;

      width: 10px;
      height: 75px;
      transform: translateY(-50%);

      background: url('../images/top-section/from-to-line.svg') no-repeat;
      background-size: cover;
    }
  }

  &__field-text {
    margin-bottom: 6px;
  }

  &__actions {
    display: grid;
    gap: 14px;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 20px;
  }

  &__new-post-link {
    color: $color-main;

    &:hover {
      color: darken($color-main, 20%);
    }
  }

  &__submit-btn,
  &__calendar-btn {
    @include media-breakpoint-down(md) {
      grid-column: span 2;
    }
  }
}
