.reviews-item {
  margin-bottom: 30px;
  background-color: $gray-lightest;

  &:last-child {
    margin-bottom: 0;
  }

  &__inner {
    padding: 30px;
  }

  &__header {
    display: flex;
    align-items: center;
    gap: 32px;

    margin-bottom: 15px;
  }

  &__username {
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
  }

  &__date {
    font-weight: 300;
    font-size: 14px;
    line-height: 24px;
  }

  &__text {
    margin-bottom: 0;

    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
  }
}
