.work-history-table {
  $block-name: &;

  margin: 0;

  thead th {
    &:first-child {
      padding-left: 20px;
    }
    &:last-child {
      padding-right: 20px;
    }
  }
  &__item {
    @include media-breakpoint-down(lg) {
      display: grid !important;
      grid-template-columns: repeat(10, 1fr);
      row-gap: 10px;
      padding: 37px 37px;
      background: #f6f6f6;

      td {
        display: flex !important;
        align-items: center;
        gap: 10px;
        padding-left: 0 !important;
        padding-right: 0 !important;
        padding-top: 10px !important;
        padding-bottom: 10px !important;

        &::before {
          min-width: 30px;
        }
      }

      &-move-date {
        grid-column: 1 / 5;
        grid-row: 1 / 3;
        border-bottom: 1px solid rgba(206, 206, 206, 0.5);
      }

      &-cf {
        grid-column: 1 / 3;
      }

      &-lbs {
        grid-column: 1 / 3;
      }

      &-moving-from {
        grid-column: 5 / -1;

        br {
          display: none;
        }
      }

      &-moving-to {
        grid-column: 5 / -1;
        grid-row: 2 / 3;
        border-bottom: 1px solid rgba(206, 206, 206, 0.5);

        br {
          display: none;
        }
      }

      &-miles {
        grid-column: 3 / -1;
      }

      &-estimate {
        grid-column: 3 / -1;
      }
    }

    @include media-breakpoint-down(md) {
      &-move-date {
        grid-column: 1 / -1;
      }
      &-moving-from {
        grid-column: 1 / -1;
        grid-row: 3 / 4;
      }
      &-moving-to {
        grid-column: 1 / -1;
        grid-row: 4 / 5;
      }
      &-miles {
        grid-column: 5 / -1;
      }
      &-estimate {
        grid-column: 5 / -1;
      }
      &-cf,
      &-lbs {
        grid-column: 1 / 5;
      }
    }

    td {
      &:last-child {
        padding-right: 20px;
      }
    }
  }

  &--cf {
    @include media-breakpoint-down(lg) {
      #{$block-name}__item-moving-from {
        grid-column: 1 / 5;
      }
      #{$block-name}__item-moving-to {
        grid-column: 1 / 5;
      }
      #{$block-name}__item-cf {
        grid-column: 5 / 9;
      }
      #{$block-name}__item-zip-code {
        grid-column: 5 / 9;
      }
      #{$block-name}__item-contact {
        grid-column: 1 / -1;
      }
    }
    @include media-breakpoint-down(md) {
      #{$block-name}__item-moving-from {
        grid-column: 1 / -1;
      }
      #{$block-name}__item-moving-to {
        grid-column: 1 / -1;
      }
      #{$block-name}__item-cf {
        grid-column: 1 / 9;
      }
      #{$block-name}__item-zip-code {
        grid-column: 1 / 9;
      }
    }
  }
}
