.pricing-details {
  &__title {
    margin-bottom: 50px;
    text-align: center;
  }

  &__info {
    @include media-breakpoint-down(lg) {
      grid-template-columns: 1fr 300px;
      padding: 30px;
    }

    @include media-breakpoint-down(md) {
      grid-template-columns: 1fr;
      padding: 20px;
    }

    display: grid;
    align-items: center;
    grid-template-columns: 1fr 443px;
    gap: 10px;
    border: 5px solid $color-main;
    padding: 50px;

    h3 {
      margin-bottom: 20px;
    }

    .button {
      max-width: 324px;
      margin-bottom: 35px;
    }

    p {
      margin-bottom: 35px;
    }

    ul {
      @include media-breakpoint-down(md) {
        margin-bottom: 36px;
      }
    }

    ul li {
      position: relative;
      margin-bottom: 35px;
      padding-left: 33px;

      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        width: 20px;
        height: 20px;
        transform: translateY(-50%);
        background: svg-load('checked-icon.svg') center no-repeat;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__right-img-wrap {
    @include media-breakpoint-down(md) {
      display: flex;
      justify-content: center;
    }
  }
}
