// В этом файле должны быть стили для БЭМ-блока main-nav, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.main-nav {
  $block-name: &; // #{$block-name}__element

  position: relative;
  z-index: 10;

  // первый уровень
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;

    // мобильный вид списка
    @media (max-width: ($menu-desktop-width - 1)) {
      // открытие/закрытие меню только в случае работающего JS

      .js & {
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.2s;

        &--lvl-2 {
          max-height: none;
        }
      }

      .js #{$block-name}--open & {
        max-height: 1000px;
        transition: max-height 0.6s;

        &--lvl-2 {
          max-height: none;
        }
      }
    }

    // настольный вид списка
    @media (min-width: $menu-desktop-width) {
      display: flex;
      flex-wrap: wrap;

      &--lvl-2 {
        display: block;
      }

      &--lvl-3 {
        display: block;
      }
    }
  }

  &__item {
    position: relative;

    li {
      padding: 0;
    }

    &--active {
      & > #{$block-name}__link {
        // stylelint-disable-line  selector-combinator-blacklist
        color: #000;
      }
    }

    @media (min-width: $menu-desktop-width) {
      padding: 14px 0;

      &--has-child {
        &:after {
          content: '';
          position: absolute;
          z-index: 1;
          top: 26px;
          right: 10px;
          width: 7px;
          height: 7px;
          border-left: 1px solid $white;
          border-bottom: 1px solid $white;
          pointer-events: none;
          transform: rotate(-45deg);
          transition: all 0.3s;
        }

        & > a {
          padding-right: 30px;
        }

        &:hover {
          & > a {
            background-color: $gray-darken;
          }
        }
      }
    }
  }

  &__link {
    position: relative;
    display: block;
    padding: 0.5em 1em;
    border-radius: 1000px;
    color: $white;
    text-decoration: none;

    &:hover,
    &:focus {
      z-index: 1;
      color: $white;
      text-decoration: none;
      background-color: $gray-darken;
    }

    &:focus {
      background-color: transparent;
      box-shadow: 0px 0px 0px 3px rgba($white, 0.1);
      border-radius: 1000px;
    }

    &--lvl-2 {
      min-width: 200px;
      border-radius: inherit;

      &:focus {
        border-radius: 0;
        box-shadow: 0px 0px 0px 4px rgba(39, 110, 241, 0.5);
      }

      @media (max-width: ($menu-desktop-width - 1)) {
        padding-left: 2em;
      }
    }

    &--lvl-3 {
      min-width: 200px;
      border-radius: inherit;

      &:focus {
        border-radius: 0;
      }

      @media (max-width: ($menu-desktop-width - 1)) {
        padding-left: 3em;
      }
    }
  }

  &__sublist-wrapper {
    a:hover {
      background-color: $gray;
    }
    &--lvl-2 {
      @media (min-width: $menu-desktop-width) {
        position: absolute;
        z-index: 1;
        top: 100%;
        left: 0;
        height: 0;
        opacity: 0;
        background-color: $gray-darken;
        overflow: hidden;
        transition: opacity $transition-time;

        // stylelint-disable
        #{$block-name}__item--show-child > &,
        #{$block-name}__item:hover & {
          height: auto;
          overflow: visible;
          opacity: 1;
        }
        // stylelint-enable
      }
    }

    &--lvl-3 {
      @media (min-width: $menu-desktop-width) {
        position: absolute;
        z-index: 1;
        top: 0;
        left: 100%;
        opacity: 0;
        height: 0;
        overflow: hidden;
        background-color: $gray-darken;
        transition: opacity $transition-time;

        // stylelint-disable
        #{$block-name}__item--show-child > &,
        #{$block-name}__item--lvl-2:hover & {
          height: auto;
          overflow: visible;
          opacity: 1;
        }
        // stylelint-enable
      }
    }
  }

  // переключатель

  &__toggler {
    display: inline-block;
    border: none;
    padding: 0;
    background: none;

    @media (min-width: $menu-desktop-width) {
      display: none;
    }

    .no-js & {
      display: none;
    }
  }
}
