.chips {
  display: flex;
  column-gap: 15px;

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 10px;
    padding: 10px 20px;
    padding-right: 30px;
    background-color: $gray-lightest;
  }

  &__item-key,
  &__item-value {
    font-size: 12px;
    line-height: 19px;
  }

  &__item-delete-button {
    position: absolute;
    right: 5px;
    top: 5px;
    cursor: pointer;
  }
}
