// В этом файле должны быть стили для БЭМ-блока btn, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  column-gap: 10px;
  max-width: 100%;
  overflow: hidden;
  border: none;
  padding: 0.7em 1.5em;
  transition: background-color $transition-time, color $transition-time;
  color: $white;
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  text-transform: none;
  vertical-align: middle;
  white-space: nowrap;
  text-overflow: ellipsis;
  user-select: none;
  text-align: center;
  background: $black;
  cursor: pointer;
}

.button:hover {
  background-color: $gray-light;
}

.button:focus {
  border-color: $border-color;
  box-shadow: 0px 0px 0px 4px rgba(39, 110, 241, 0.5);
}

.button:focus-visible {
  outline: none;
}

.button:active {
  color: $text-color-muted;
  background-color: $gray-light;
}

.button--full-width {
  width: 100%;
}

.button--transparent {
  color: $gray-light;
  background-color: transparent;

  &:hover,
  &:focus {
    color: $black;
    background-color: transparent;
  }
}

.button--icon {
  width: 40px;
  height: 40px;
  border: 1px solid $gray-light;
  padding: 11px 10px;
  background-color: transparent;

  svg {
    fill: $gray-light;
  }

  &:hover {
    background-color: transparent;
    svg {
      fill: $color-main;
    }
  }

  &:focus {
    background-color: transparent;
  }
}

.button--primary {
  background-color: $color-main;

  &:hover {
    color: $white;
    background-color: darken($color-main, 20%);
  }

  &:focus {
    color: $white;
    background-color: $color-main;
    box-shadow: 0px 0px 0px 4px rgba(0, 0, 0, 0.75);
  }

  &:active {
    background-color: darken($color-main, 20%);
  }
}

.button--canceled {
  color: #e54937;
  background-color: #e5e5e5;

  &:hover {
    color: $white;
    background-color: #e54937;
  }

  &:focus {
    border-color: transparent;
    background: #e5e5e5;
    box-shadow: 0px 0px 0px 4px rgba(229, 73, 55, 0.5);
  }

  &:active {
    color: $text-color-muted;
    background-color: #e54937;
  }
}
